import React, { useEffect, useState } from "react";

import { i18n } from "src/i18n";
import Image from "../../Image";
import Translate from "../../Translate";
import actionsPublication from "src/modules/publication/list/publicationListActions";
import selectorsPublication from "src/modules/publication/list/publicationListSelectors";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { Button, Carousel, Col, Row, Tooltip } from "antd";
import action from "src/modules/publication/view/publicationViewActions";
import authSelectors from "src/modules/auth/authSelectors";

import {
  TwitterIcon,
  TwitterShareButton,
  FacebookShareButton,
  FacebookIcon,
  LinkedinIcon,
  LinkedinShareButton,
} from "react-share";
import MyPDFViewer from "./MyPDFViewer";
import moment from "moment";
import { StarOutlined, StarFilled } from "@ant-design/icons";
import AuthService from "src/modules/auth/authService";

import { VolkenoForumy } from "volkeno-forumy";
import "volkeno-forumy/dist/index.css";

function DescriptionPublication(props) {
  //forum
  const [Datas, setDatas] = useState<string[]>([]);
  const [DatasUserSession, setDatasUserSession] = useState<string[]>([]);
  let datas = [
    {
      initialTitle:
        "Quels sont les avantages d'utiliser le vélo comme moyen de transport ?",
      initialContent:
        " Le vélo est un moyen de transport silencieux et écologique. C'est un engin qui ne présente pas beaucoup de danger. D'une part, le vélo ne nous fait pas respirer les vapeurs d'essence, mais les vapeurs du matin et du soir. D'autre part, il se moque des règlements, il ignore les interdits.",
      id: 1,
      slug: "avantage_velo",
      created_at: "6/04/2022",
      author: {
        firstName: "Joël ",
        lastName: "Gomis",
        avatar: "/mediafiles/avatars/default.png",
      },
      first_level_response: [
        {
          id: 1,
          slug: "initial_message_slug",
          user: {
            firstName: "Sadio",
            lastName: "Sanghare",
            avatar: "/mediafiles/avatars/default.png",
          },
          content: "Comment 1",
          created_at: "6/04/2022",
          second_level_response: [
            {
              id: 1,
              slug: "first_level_message_slug",
              user: {
                firstName: "Ndeye",
                lastName: "Faye",
                avatar: "/mediafiles/avatars/default.png",
              },
              content: "Reponse 7 deus",
            },
          ],
        },
      ],
    },
  ];

  //forum
  const dispatch = useDispatch();
  const { rows } = props;
  const rowsPublication = useSelector(selectorsPublication.selectRows);
  const loadingPublication = useSelector(selectorsPublication.selectLoading);
  const currentUser = useSelector(authSelectors.selectCurrentUser);
  const [isFav, setIsFav] = useState(false);
  const showDetaill = (id) => {
    dispatch(action.doFind(id));
  };
  useEffect(() => {
    dispatch(actionsPublication.doFetchValidePublication(4));

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    if (currentUser && currentUser.favorites) {
      currentUser.favorites.publication?.map((pub) => {
        if (pub.id === rows?.id) {
          setIsFav(true);
        }
      });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentUser, rows]);
  const langue = localStorage.getItem("language");
  const addToFavourite = async (id) => {
    currentUser.favorites.publication.push(id);
    const data = {
      ...currentUser,
    };
    await AuthService.updateProfile(data);
    setIsFav(true);
  };
  const popFromFavourite = async (id) => {
    var index = 0;
    currentUser.favorites.publication.map((pub, pubIndex) => {
      if (pub.id === id) {
        index = pubIndex;
        return;
      }
    });
    currentUser.favorites.publication.splice(index, 1);
    const data = {
      ...currentUser,
    };
    await AuthService.updateProfile(data);
    setIsFav(false);
  };
  const onChange = (currentSlide: number) => {};
  const onAddUserSession = (e: any) => {
    e.preventDefault();
    let fields: any = {
      id: generateUniqueID(),
      slug: generateUniqueID(),
      created_at: "22/04/2022",
      user: {
        firstName: "firstNameUserSession",
        lastName: "lastNameUserSession",
        avatar: "/mediafiles/avatars/default.png",
      },
    };

    setDatasUserSession([...DatasUserSession, fields]);
  };
  console.log("current user", currentUser);
  const onAddSubject = (e: any) => {
    e.preventDefault();
    let fields: any = {
      initialTitle: "initialTitle",
      initialContent: "initialContent",
      id: generateUniqueID(),
      slug: generateUniqueID(),
      created_at: moment().format("DD/MM/yyyy"),
      author: {
        firstName: "firstName",
        lastName: "lastName",
        avatar: "/mediafiles/avatars/default.png",
      },
      first_level_response: [],
    };
    setDatas([...Datas, fields]);
  };
  function generateUniqueID() {
    var text = "";
    var possible =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 5; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }
  return (
    <>
      {/* <div>
        <Button onClick={onAddSubject}>Add</Button>
        <VolkenoForumy
          Datas={Datas}
          DatasUserSession={[
            {
              id: generateUniqueID(),
              slug: generateUniqueID(),
              created_at: moment().format("DD/MM/yyyy"),
              user: {
                firstName: "Lorem",
                lastName: "Ipsum",
                avatar: "/mediafiles/avatars/default.png",
              },
            },
          ]}
          hasThirdLevel={false}
        />
      </div> */}
      <div className="header__left">
        {props.publication ? (
          <Link to={`/detail/${props.publication._id}`}>
            <p style={{ fontSize: 20 }}>
              {langue === "fr" || langue === "en" ? (
                <i
                  className="fas fa-arrow-alt-circle-left"
                  style={{ margin: 5 }}
                ></i>
              ) : (
                <i
                  className="fas fa-arrow-alt-circle-right"
                  style={{ margin: 5 }}
                ></i>
              )}
              {i18n("common.cahierMere")}
            </p>
          </Link>
        ) : (
          <>
            {rows?.thematique.length > 0 ? (
              <div>
                {rows.thematique.map((theme) => {
                  return (
                    <p className="hashtagTheme">
                      {Translate.Trans("title", theme)}
                    </p>
                  );
                })}
              </div>
            ) : null}
          </>
        )}
      </div>

      <div className="detail">
        <div className="detail__left">
          <div className="detail__title">{Translate.Trans("title", rows)}</div>

          <div className="detail__author">
            <div className="author__left">
              <div className="left__author__date">
                {moment(rows?.date).format("DD-MM-YYYY")}
              </div>
            </div>
            <div className="detaillEvenement__socialMedia">
              <div className="socialMedia__title">
                {i18n("common.Partager")} :
              </div>
              <div>
                <FacebookShareButton url={window.location.href}>
                  <FacebookIcon size={35} round />
                </FacebookShareButton>
              </div>

              <div>
                <TwitterShareButton url={window.location.href}>
                  <TwitterIcon size={35} round />
                </TwitterShareButton>
              </div>
              <div>
                <LinkedinShareButton url={window.location.href}>
                  <LinkedinIcon size={35} round />
                </LinkedinShareButton>
              </div>
              {currentUser !== null ? (
                <div>
                  {isFav ? (
                    <Tooltip title={i18n("common.unfavoris")}>
                      <StarFilled
                        style={{ color: "#e1011a", fontSize: 35 }}
                        onClick={() => popFromFavourite(rows.id)}
                      />
                    </Tooltip>
                  ) : (
                    <Tooltip title={i18n("common.favoris")}>
                      <StarOutlined
                        style={{ color: "#e1011a", fontSize: 35 }}
                        onClick={() => addToFavourite(rows.id)}
                      />
                    </Tooltip>
                  )}
                </div>
              ) : null}
            </div>
          </div>
          <div className="detail__description">
            {langue === "fr" ? (
              <>
                {rows?.photos.length > 0 ? (
                  <Carousel autoplay afterChange={onChange}>
                    {rows?.photos?.map((item) => (
                      <div style={{ paddingTop: "19px" }}>
                        <Image
                          width={"100%"}
                          height={"500"}
                          src={item.downloadUrl}
                        />
                      </div>
                    ))}
                  </Carousel>
                ) : null}
              </>
            ) : langue === "ar" ? (
              <>
                {rows?.photosAR.length > 0 ? (
                  <Carousel autoplay afterChange={onChange}>
                    {rows?.photosAR?.map((item) => (
                      <div style={{ paddingTop: "19px" }}>
                        <Image
                          width={"100%"}
                          height={"500"}
                          src={item.downloadUrl}
                        />
                      </div>
                    ))}
                  </Carousel>
                ) : null}
              </>
            ) : (
              <>
                {rows?.photosEN.length > 0 ? (
                  <Carousel autoplay afterChange={onChange}>
                    {rows?.photosEN?.map((item) => (
                      <div style={{ paddingTop: "19px" }}>
                        <Image
                          width={"100%"}
                          height={"500"}
                          src={item.downloadUrl}
                        />
                      </div>
                    ))}
                  </Carousel>
                ) : null}
              </>
            )}

            <div
              className="description__detaillEvenement"
              dangerouslySetInnerHTML={{
                __html: Translate.Trans("description", rows),
              }}
            />

            <hr />
          </div>
          {rows?.glossaire.length > 0 ? (
            <div>
              {rows?.glossaire.map((glossItem) => {
                return (
                  <Link className="hashtag" to={`/Glossaire/${glossItem.id}`}>
                    {langue === "fr"
                      ? glossItem.abreviationFR
                      : langue === "ar"
                      ? glossItem.abreviationAR
                      : glossItem.abreviationEN}
                  </Link>
                );
              })}
            </div>
          ) : null}
          <div className="App">
            {langue === "fr" ? (
              <>
                {" "}
                {rows?.supports.length > 0 ? (
                  <div>
                    <>
                      {rows.supports[0].name.split(".").pop() === "pdf" ? (
                        <MyPDFViewer pdf={rows?.supports[0]?.downloadUrl} />
                      ) : null}
                    </>

                    <a
                      href={rows?.supports[0].downloadUrl}
                      download
                      style={{
                        color: "#fff",
                        fontSize: 20,
                        border: "solid",
                        borderRadius: "5px",
                        backgroundColor: "#e1011a",
                        borderColor: "#e1011a",
                      }}
                    >
                      {i18n("telecharger")}
                    </a>
                    <hr />
                  </div>
                ) : null}
              </>
            ) : langue === "ar" ? (
              <>
                {" "}
                {rows?.supportsAR.length > 0 ? (
                  <div>
                    <>
                      {rows.supportsAR[0].name.split(".").pop() === "pdf" ? (
                        <MyPDFViewer pdf={rows?.supportsAR[0]?.downloadUrl} />
                      ) : null}
                    </>

                    <a
                      href={rows?.supportsAR[0].downloadUrl}
                      download
                      style={{
                        color: "#fff",
                        fontSize: 20,
                        border: "solid",
                        borderRadius: "5px",
                        backgroundColor: "#e1011a",
                        borderColor: "#e1011a",
                      }}
                    >
                      {i18n("telecharger")}
                    </a>
                    <hr />
                  </div>
                ) : null}
              </>
            ) : (
              <>
                {" "}
                {rows?.supportsEN.length > 0 ? (
                  <div>
                    <>
                      {rows.supportsEN[0].name.split(".").pop() === "pdf" ? (
                        <MyPDFViewer pdf={rows?.supportsEN[0]?.downloadUrl} />
                      ) : null}
                    </>

                    <a
                      href={rows?.supportsEN[0].downloadUrl}
                      download
                      style={{
                        color: "#fff",
                        fontSize: 20,
                        border: "solid",
                        borderRadius: "5px",
                        backgroundColor: "#e1011a",
                        borderColor: "#e1011a",
                      }}
                    >
                      {i18n("telecharger")}
                    </a>
                    <hr />
                  </div>
                ) : null}
              </>
            )}
          </div>
        </div>

        <div>
          <div className="archieve__header">
            <h2>{i18n("entities.publication.lastNews")}</h2>
            <div className="satestique__bar"></div>
          </div>
          <div className="manification__images">
            {loadingPublication ? (
              <h4>{i18n("table.loading")}</h4>
            ) : (
              <>
                {rowsPublication.length > 0
                  ? rowsPublication.map((item) => (
                      <Link to={`/detail/${item.id}`}>
                        <div
                          onClick={() => showDetaill(item.id)}
                          style={{
                            cursor: "pointer",
                            display: "flex",
                            columnGap: 17,
                          }}
                        >
                          {langue === "fr" ? (
                            <Image
                              src={
                                item.photos.length > 0
                                  ? item.photos[0].downloadUrl
                                  : "Image publication image "
                              }
                              width="120"
                              height="78"
                              alt=""
                            />
                          ) : langue === "ar" ? (
                            <Image
                              src={
                                item.photosAR.length > 0
                                  ? item.photosAR[0].downloadUrl
                                  : "Image publication image "
                              }
                              width="120"
                              height="78"
                              alt=""
                            />
                          ) : (
                            <Image
                              src={
                                item.photosEN.length > 0
                                  ? item.photosEN[0].downloadUrl
                                  : "Image publication image "
                              }
                              width="120"
                              height="78"
                              alt=""
                            />
                          )}

                          <div className="list__detaill">
                            <div className="detaill__header">
                              <div className="header__left">
                                <p>
                                  {Translate.Trans("title", item.thematique[0])}
                                </p>
                              </div>
                              <div className="header__right">
                                {moment(item.date).format("DD-MM-YYYY")}
                              </div>
                            </div>
                            <div
                              className="detaill__content text__wrap"
                              style={{ width: "233px" }}
                            >
                              {Translate.Trans("title", item)}
                            </div>
                          </div>
                        </div>
                      </Link>
                    ))
                  : null}
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
}

DescriptionPublication.prototype = {
  showDetaill: PropTypes.func.isRequired,
};

export default DescriptionPublication;
